import Web3 from 'web3';
export const LINK_MAIN = 'https://bsc-dataseed.binance.org/';
export const LINK_TEST = 'https://data-seed-prebsc-1-s1.binance.org:8545/';

const contractConnect = async (abi, smartContract, net) => {
  let web3Account;
  if (net === 56) {
    web3Account = new Web3(LINK_MAIN);
  } else if (net === 97) {
    web3Account = new Web3(LINK_TEST);
  }
  return new web3Account.eth.Contract(abi, smartContract);
};

export default contractConnect;
